.tabs {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
}

.tab {
  width: 200px;
  padding: 15px;
  background-color: rgb(24, 26, 30);
  border: 1px rgba(255,255,255,.15) solid;
  box-shadow: 0px 3px 10px rgba(255,255,255,.1);
  border-bottom: 5px rgba(255,255,255,.1) solid;
  color: rgba(255,255,255,0.5);
  text-align: center;
  float: left;
  position: relative;
  transition: box-shadow .5s ease 0s, top .5s ease 0s, border .5s ease 0s, color .5s ease 0s;
  top: 0;
}

.tab:first-child {
  margin-left: 0;
}

.tab:hover {
  cursor: pointer;
  box-shadow: 0px 6px 15px rgba(255,255,255,.5);
  top: -3px;
  border: 1px rgba(255,255,255,.3) solid;
  color: rgba(255,255,255,0.8);
  /* border-bottom: 5px rgba(255,255,255,.3) solid; */ 
}

.tabs .active {
  /* border-bottom: 5px rgba(255, 33, 33, 1) solid; */
  box-shadow: 0px 6px 15px rgba(255, 30, 30, .5);
  border: 1px rgba(255,30,30,.4) solid;
  top: -10px;
  color: rgba(255,50,50,1);
}

.tabs menu ul {
  list-style: none;
}

.tabs menu + div {
  clear: both;
  margin-top: 15px;
  padding: 15px 0;
  color: white;
  line-height: 1.5;
  color: rgba(255,255,255,.6);
}

